.isAlive {
  font-family: 'calligraphrregular';
  font-size: 18px;
  position: absolute;
  top: 10px;
  right: 40px;
  color: rgba(255, 255, 255, 0.507);
  /* background-color: rgba(0, 0, 0, 0.411); */
  padding: 2px 6px;
}

.isAlive::after {
  position: absolute;
  top: -2px;
  right: -30px;
  content: '';
  width: 10px;
  height: 10px;
  border: solid 1px rgba(65, 63, 63, 0.507);
  border-radius: 50%;
  background: rgb(139, 224, 39);
  animation: pulseAlive 4s linear 1s infinite;
}

.isDead {
  font-family: 'calligraphrregular';
  font-size: 18px;
  position: absolute;
  top: 10px;
  right: 40px;
  color: rgba(255, 255, 255, 0.507);
  /* background-color: rgba(0, 0, 0, 0.411); */
  padding: 2px 6px;
}

.isDead::after {
  position: absolute;
  top: -2px;
  right: -30px;
  content: '';
  width: 10px;
  height: 10px;
  border: solid 1px rgba(65, 63, 63, 0.507);
  border-radius: 50%;
  background: rgb(224, 39, 39);
  animation: pulseDead 4s linear 1s infinite;
}

.isunknown {
  font-family: 'calligraphrregular';
  font-size: 18px;
  position: absolute;
  top: 10px;
  right: 40px;
  color: rgba(255, 255, 255, 0.507);
  /* background-color: rgba(0, 0, 0, 0.411); */
  padding: 2px 6px;
}

.isunknown::after {
  position: absolute;
  top: -2px;
  right: -30px;
  content: '';
  width: 10px;
  height: 10px;
  border: solid 1px rgba(65, 63, 63, 0.507);
  border-radius: 50%;
  background: rgb(255, 251, 10);
  animation: pulseUnknown 4s linear 1s infinite;
}

@keyframes pulseAlive {
  0% {
    box-shadow: 0 0 2px #099209, inset 0 0 2px #099209;
  }
  50% {
    box-shadow: 0 0 16px #099209, inset 0 0 14px #099209;
  }
  100% {
    box-shadow: 0 0 2px #099209, inset 0 0 2px #099209;
  }
}

@keyframes pulseDead {
  0% {
    box-shadow: 0 0 2px #691c1c, inset 0 0 2px #691c1c;
  }
  50% {
    box-shadow: 0 0 16px #691c1c, inset 0 0 14px #691c1c;
  }
  100% {
    box-shadow: 0 0 2px #691c1c, inset 0 0 2px #691c1c;
  }
}

@keyframes pulseUnknown {
  0% {
    box-shadow: 0 0 2px #b6a616, inset 0 0 2px #b6a616;
  }
  50% {
    box-shadow: 0 0 16px #b6a616, inset 0 0 14px #b6a616;
  }
  100% {
    box-shadow: 0 0 2px #b6a616, inset 0 0 2px #b6a616;
  }
}
