p.Alive {
  font-family: 'calligraphrregular';
  font-size: 18px;
  position: absolute;
  top: 10px;
  right: 40px;
  color: rgba(255, 255, 255, 0.507);
  /* background-color: rgba(0, 0, 0, 0.411); */
  padding: 2px 6px;
}

p.Alive::after {
  position: absolute;
  top: -2px;
  right: -30px;
  content: '';
  width: 20px;
  height: 20px;
  border: solid 1px rgba(65, 63, 63, 0.507);
  border-radius: 50%;
  background: rgb(139, 224, 39);
  animation: pulse 2s linear 1s infinite;
}

p.Dead {
  font-family: 'calligraphrregular';
  font-size: 18px;
  position: absolute;
  top: 10px;
  right: 40px;
  color: rgba(255, 255, 255, 0.507);
