  width: 20px;
  height: 20px;
  border: solid 1px rgba(65, 63, 63, 0.507);
  border-radius: 50%;
  background: rgb(255, 251, 10);
  animation: pulse 2s linear 1s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 2px #4f534f, inset 0 0 2px #4f534f;
  }
  50% {
    box-shadow: 0 0 16px #4f534f, inset 0 0 14px #4f534f;
  }
  100% {
    box-shadow: 0 0 2px #4f534f, inset 0 0 2px #4f534f;
  }
}

div.Dead {
  background: linear-gradient(135deg, #f5515f 0%, #a1051d 100%);
  color: #530713;
}

div.unknown {
  background: linear-gradient(135deg, #fad961 0%, #f76b1c 100%);
  color: #d45e19;
}
